import { useEffect, useState } from 'react'

import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore'
import NavigateNextIcon from '@mui/icons-material/NavigateNext'
import { Box, Button, Modal, TextareaAutosize, Typography, styled } from '@mui/material'
import { useTheme } from '@mui/material/styles'
import useMediaQuery from '@mui/material/useMediaQuery'

import type { Applicant } from 'src/models/Applicant'

import BulkActions from 'src/content/manage/Apply/BulkActions'
import type { Question } from 'src/content/manage/Form'

interface UserModalProps {
  open: boolean
  onClose: () => void
  questions: Question[]
  Applicants: Applicant[]
  current: number
  handleRefresh: () => void
}

const UserModal: React.FC<UserModalProps> = ({ open, onClose, questions, Applicants, current, handleRefresh }) => {
  const [currentApplicant, setCurrentApplicant] = useState<number>(current) // Index of Applicants[]
  const numberOfApplicants = Applicants.length

  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  useEffect(() => {
    setCurrentApplicant(current)
  }, [open])

  const handleNextApplicant = () => {
    setCurrentApplicant((currentApplicant + 1) % numberOfApplicants)
  }

  const handlePrevApplicant = () => {
    if (currentApplicant === 0) {
      setCurrentApplicant(numberOfApplicants - 1)
    } else {
      setCurrentApplicant(currentApplicant - 1)
    }
  }

  return (
    <Modal open={open} onClose={onClose}>
      <Box sx={style}>
        <Box justifyContent="center">
          <Typography variant="h3" sx={{ textAlign: 'center' }}>
            {Applicants[currentApplicant]?.name} 님 의 지원서
          </Typography>
          <Typography variant="subtitle2" sx={{ mb: 2, textAlign: 'center' }}>
            {Applicants[currentApplicant]?.studentId} / {Applicants[currentApplicant]?.department}
          </Typography>
        </Box>
        <Box display="flex" alignItems="center" justifyContent="space-around">
          {matches && (
            <Button onClick={handlePrevApplicant}>
              <NavigateBeforeIcon />
            </Button>
          )}

          <Box width={matches ? '60%' : '90%'}>
            {questions.map((question, index) => {
              const answers = Applicants[currentApplicant]?.answers
              if (!answers) return
              return (
                <Box key={index}>
                  <Typography variant="subtitle1">{question}</Typography>
                  <CustomTextareaAutosize maxRows={4} minRows={3} value={answers[index]} disabled />
                </Box>
              )
            })}
          </Box>
          {matches && (
            <Button onClick={handleNextApplicant}>
              <NavigateNextIcon />
            </Button>
          )}
        </Box>

        {matches ? (
          <Box marginTop={2} display="flex" justifyContent="center">
            <BulkActions
              selectedApplicants={[Applicants[currentApplicant]?.id]}
              onCloseModal={onClose}
              handleRefresh={handleRefresh}
            />
            <Button variant="outlined" onClick={onClose} sx={{ ml: 1, mt: matches ? 0 : 1 }}>
              닫기
            </Button>
          </Box>
        ) : (
          <>
            <Box marginTop={2} display="flex" justifyContent="center">
              <BulkActions
                selectedApplicants={[Applicants[currentApplicant]?.id]}
                onCloseModal={onClose}
                handleRefresh={handleRefresh}
              />
            </Box>
            <Box display="flex" justifyContent="space-around" sx={{ mt: 2 }}>
              <Button onClick={handlePrevApplicant}>
                <NavigateBeforeIcon />
              </Button>
              <Button variant="outlined" onClick={onClose}>
                닫기
              </Button>
              <Button onClick={handleNextApplicant}>
                <NavigateNextIcon />
              </Button>
            </Box>
          </>
        )}
      </Box>
    </Modal>
  )
}

export default UserModal

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '80%',
  maxWidth: '600px',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
}

const CustomTextareaAutosize = styled(TextareaAutosize)`
  margin: 10px 0px;
  padding: 8px;
  width: 100%;
  border-radius: 5px;
  border: 1px solid lightgrey;
  font-family: 'Pretendard';
`
