import { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router'

import CheckCircleTwoToneIcon from '@mui/icons-material/CheckCircleTwoTone'
import PersonOffTwoToneIcon from '@mui/icons-material/PersonOffTwoTone'
import { Box, Button } from '@mui/material'
import { styled } from '@mui/material/styles'

import { APPLY_MESSAGES } from 'src/data/messages'

import { getClubRecruitInformation } from 'src/api/club'
import { putApplicants } from 'src/api/clubApply'

import type { Recruit } from 'src/models/Recruit'

import ConfirmDialog from 'src/components/ConfirmDialog'

const ButtonError = styled(Button)(
  ({ theme }) => `
     background: ${theme.colors.error.main};
     color: ${theme.palette.error.contrastText};

     &:hover {
        background: ${theme.colors.error.dark};
     }
    `
)

const ButtonSuccess = styled(Button)(
  ({ theme }) => `
       background: ${theme.colors.success.main};
       color: ${theme.palette.success.contrastText};
  
       &:hover {
          background: ${theme.colors.success.dark};
       }
      `
)

interface BulkActionsProps {
  selectedApplicants: number[]
  onCloseModal?: () => void
  handleRefresh: () => void
}

type ResultType = 'PASS' | 'FAIL'

export interface SubmitApplicant {
  userId: number
  result: ResultType
}

function BulkActions({ selectedApplicants, onCloseModal, handleRefresh }: BulkActionsProps) {
  const authHeader = useAuthHeader()
  const { enqueueSnackbar } = useSnackbar()
  const params = useParams()
  const [isDialog, setIsDialog] = useState<boolean>(false)
  const [resultType, setResultType] = useState<ResultType>('FAIL')

  const handleFail = () => {
    setResultType('FAIL')
    setIsDialog(true)
  }

  const handlePass = () => {
    setResultType('PASS')
    setIsDialog(true)
  }

  const handleClick = (resultType: ResultType) => {
    const submitApplicants = selectedApplicants.map(id => {
      return {
        userId: id,
        result: resultType as ResultType
      }
    })
    handleSubmit(submitApplicants)
    setIsDialog(false)
  }

  const handleSubmit = async (submitApplicants: SubmitApplicant[]) => {
    try {
      const recruitInfo: Recruit = await getClubRecruitInformation({ clubId: params.id as unknown as number })
      const recruitId = Number(recruitInfo.id)

      await putApplicants({
        clubId: params.id as unknown as number,
        recruitId: recruitId,
        data: submitApplicants,
        token: authHeader()
      })
      handleRefresh()
      enqueueSnackbar(APPLY_MESSAGES.SUCCESS, { variant: 'success' })
      onCloseModal && onCloseModal()
    } catch (err) {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? APPLY_MESSAGES.SERVER_ERROR, {
          variant: 'error'
        })
        return
      }
      enqueueSnackbar(APPLY_MESSAGES.UNKNOWN_ERROR, {
        variant: 'error'
      })
    }
  }

  return (
    <>
      <Box display="flex" alignItems="center" justifyContent="space-between">
        <Box display="flex" alignItems="center">
          <ButtonError sx={{ ml: 1 }} startIcon={<PersonOffTwoToneIcon />} variant="contained" onClick={handleFail}>
            불합격
          </ButtonError>
          <ButtonSuccess sx={{ ml: 1 }} startIcon={<CheckCircleTwoToneIcon />} variant="contained" onClick={handlePass}>
            합격
          </ButtonSuccess>
        </Box>
      </Box>
      <ConfirmDialog
        open={isDialog}
        handleClose={() => {
          setIsDialog(false)
        }}
        handleSubmit={() => {
          handleClick(resultType)
        }}
        confirmMessage={`정말 ${resultType === 'FAIL' ? '불' : ''}합격처리 하시겠습니까?`}
      />
    </>
  )
}

export default BulkActions
