import { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router-dom'

import { Box, Button, Container, Grid, Typography } from '@mui/material'

import { COMMON_MESSAGE } from 'src/data/messages'

import { getClubRecruitInformation } from 'src/api/club'
import { getApplicants, getQuestions } from 'src/api/clubApply'

import type { Applicant } from 'src/models/Applicant'
import type { Recruit } from 'src/models/Recruit'

import SelectingTable from 'src/components/SelectingTable'

import BulkActions from 'src/content/manage/Apply/BulkActions'
import UserModal from 'src/content/manage/Apply/UserModal'
import type { Question } from 'src/content/manage/Form'

const ApplyPage: React.FC = () => {
  const [applicants, setApplicants] = useState<Applicant[]>([])
  const [selectedApplicants, setSelectedApplicants] = useState<number[]>([])
  const [questions, setQuestions] = useState<Array<Question>>([{ id: 0, content: '' }])
  const [isModal, setIsModal] = useState<boolean>(false)
  const [clickedApplicant, setClickedApplicant] = useState<number>(0)
  const [refresh, setRefresh] = useState(false)

  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()
  const navigate = useNavigate()
  const properties = [
    { header: '이름', property: 'name' },
    { header: '학번', property: 'studentId' },
    { header: '학과', property: 'department' },
    { header: '연락처', property: 'phoneNumber' },
    { header: '이메일', property: 'email' }
  ]

  const { id } = useParams()
  const clubId = Number(id)

  const handleRefresh = () => {
    setRefresh(!refresh)
  }
  const handleItemClick = (idx: number) => {
    setClickedApplicant(idx)
    setIsModal(true)
  }

  const initializeApplicantsCall = async () => {
    try {
      const recruitInfo: Recruit = await getClubRecruitInformation({ clubId })
      const recruitId = Number(recruitInfo.id)
      const applicantsInfo: Applicant[] = await getApplicants({
        clubId: clubId,
        recruitId: recruitId,
        token: authHeader()
      })

      setApplicants(applicantsInfo)
      await initializeApplicantsForms()
    } catch (err) {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? COMMON_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, {
        variant: 'error'
      })
    }
  }

  const initializeApplicantsForms = async () => {
    try {
      const result = await getQuestions({
        clubId: clubId,
        token: authHeader()
      })

      setQuestions(result)
    } catch (err) {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? COMMON_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
        return
      }
      enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, {
        variant: 'error'
      })
    }
  }

  useEffect(() => {
    initializeApplicantsCall()
  }, [refresh])

  return (
    <>
      <Helmet>
        <title>지원자 관리</title>
      </Helmet>

      <Container
        maxWidth="md"
        sx={{
          height: '80vh',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'end',
          alignItems: 'center'
        }}
      >
        <Grid container direction="row" justifyContent="center" alignItems="stretch" spacing={3}>
          <Grid item xs={12}>
            <SelectingTable
              items={applicants}
              title={
                <Typography variant="h4" display="inline">
                  지원자 목록<Typography display="inline"> (합격 시 준회원으로 등록)</Typography>
                </Typography>
              }
              selectedItems={selectedApplicants}
              setSelectedItems={setSelectedApplicants}
              BulkActions={<BulkActions selectedApplicants={selectedApplicants} handleRefresh={handleRefresh} />}
              properties={properties}
              handleClick={handleItemClick}
            />
          </Grid>
        </Grid>
        <Box m={2}>
          <Button
            variant="outlined"
            onClick={() => {
              navigate(-1)
            }}
          >
            뒤로 가기
          </Button>
        </Box>

        <UserModal
          open={isModal}
          onClose={() => setIsModal(false)}
          questions={questions}
          Applicants={applicants}
          current={clickedApplicant}
          handleRefresh={handleRefresh}
        />
      </Container>
    </>
  )
}

export default ApplyPage
