import type { AxiosResponse } from 'axios'
import axios from 'axios'

import type { Applicant } from 'src/models/Applicant'
import type { ClubId } from 'src/models/Club'

import type { SubmitApplicant } from 'src/content/manage/Apply/BulkActions'
import type { Question } from 'src/content/manage/Form'

interface GetParameters {
  clubId: ClubId
  recruitId: number
  token: string
}

export const getApplicants = async ({ clubId, recruitId, token }: GetParameters) => {
  const response = await axios.get<Applicant[]>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}/applicants`,
    {
      headers: { Authorization: token }
    }
  )
  return response.data
}

interface GetQuestionsParameters {
  clubId: ClubId
  token: string
}

export const getQuestions = async ({ clubId, token }: GetQuestionsParameters) => {
  const response = await axios.get<Question[]>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/form`, {
    headers: { Authorization: token }
  })
  return response.data
}

interface PutParameters {
  clubId: ClubId
  recruitId: number
  data: SubmitApplicant[]
  token: string
}

export const putApplicants = async ({ clubId, recruitId, data, token }: PutParameters) => {
  await axios.put<null, AxiosResponse<null>, SubmitApplicant[]>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/recruit/${recruitId}/results`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}
